	module.exports = {"data":{"markdownRemark":{"html":"<h1>Ram Die</h1>\n<h2>Job Tracker</h2>\n<p>Ram Die is committed to building and servicing tools for several industries that cover a vast range of steel required products such as the: Automotive, Furniture, Appliance, Recreational Vehicle, and Medical fields. Managing all of these products can be an intense process. Ram Die was implementing a scheduling program using Microsoft Excel. Their process was messy, hard to manage, and inefficient. That is when Ram Die came to me to redesign there software as a internal web interface for scheduling and managing processes within the company.</p>\n<h2>Design</h2>\n<p>The design of the project was created to be a simple interface and easy access to CRUD operations within the management panel. The program would be broken down into two main categories. Administration where CRUD operations could be performed on jobs, employees, documents, and customers. The second category would be the Job Tracker. This would be a table that showed all the various fields for a job. It would take date times and display productivity levels based on color coding. The aesthetics of the app would take form in using Bootstrap 4.</p>\n<h2>Development</h2>\n<p>The development of the project took the for of Rapid Application Development (RAD). With that said it was agreed that the web app would be written using ASP.NET Web Forms. Web Forms would allow us to plop all the forms and tables in the application quickly without having to route controllers and models. Another reason is that the site only comprised of 6 pages. The database was a SQLServer express installation. By using Entity Framework Code-First the creation of the database with instantaneous. EF allowed me to focus on the functionality of the job tracker and take a load of writing CRUD operations.</p>\n<p>The Job Tracker page is where most of the magic was. Given certain dates the program would look at the dates and compare them against each other. Depending how the dates aligned the color of the boxes would change to indicate if the job was behind, on time, or ahead of schedule.</p>\n<h2>Gallery</h2>\n<p>Below is the admin panel that is used for CRUD operations on customers, employees, contacts, and jobs. Also pictured is the job tracker page.</p>","frontmatter":{"path":"/ram-die","title":"Ram Die","sidebarImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.0484927916120577,"src":"/static/Ramdie_Main-7653627f054388f20c2da869a96963c1-a778c.png","srcSet":"/static/Ramdie_Main-7653627f054388f20c2da869a96963c1-c5352.png 150w,\n/static/Ramdie_Main-7653627f054388f20c2da869a96963c1-dbfa3.png 300w,\n/static/Ramdie_Main-7653627f054388f20c2da869a96963c1-a778c.png 600w,\n/static/Ramdie_Main-7653627f054388f20c2da869a96963c1-4f69a.png 800w","sizes":"(max-width: 600px) 100vw, 600px"}}}}}},"pathContext":{}}

